import { base64ToArrayBuffer } from '../utils/b64';
import { Interfaces, SBPayload, base62ToArrayBuffer32 } from '../snackabra/snackabra';

// starting "v02" on 2023-04-22
// intention:
// - switch to async model
// - switch to pure b62 encoding only
// - direct support for "app loading"

function deCryptShard(data: SBPayload): Promise<ArrayBuffer> {
    return new Promise((resolve, reject) => {
        crypto.subtle.importKey("raw", data.shardKey, "PBKDF2", false, ['deriveBits', 'deriveKey'])
            .then((keyMaterial) => {
                crypto.subtle.deriveKey({
                    'name': 'PBKDF2',
                    'salt': data.salt,
                    'iterations': 100000,
                    'hash': 'SHA-256'
                }, keyMaterial, { 'name': 'AES-GCM', 'length': 256 }, true, ['encrypt', 'decrypt'])
                    .then((key) => {
                        crypto.subtle.decrypt({ name: 'AES-GCM', iv: data.iv }, key, data.image)
                            .then((padded) => {
                                let actualSize = new DataView(padded.slice(-4)).getUint32(0)
                                resolve(padded.slice(0, actualSize));
                            }).catch(() => { reject('error decrypting shard'); })
                    }).catch(() => { reject('unable to derive key'); })
            })
            .catch(() => { reject('unable to import key') })
    })
}

// NOTE: this is a slightly stripped-down duplicate of "extractPayload" in snackabra.ts
export function extractPayload(payload: ArrayBuffer): SBPayload {
    const metadataSize = new Uint32Array(payload.slice(0, 4))[0];
    const decoder = new TextDecoder();
    const _metadata = JSON.parse(decoder.decode(payload.slice(4, 4 + metadataSize)))
    const startIndex = 4 + metadataSize;
    const data: SBPayload = {}
    for (let i = 1; i < Object.keys(_metadata).length; i++) {
        const _index = i.toString();
        if (_metadata[_index]) {
            const propertyStartIndex = _metadata[_index]['start'];
            const size = _metadata[_index]['size'];
            const entry = _metadata[_index]
            data[entry['name']] = payload.slice(startIndex + propertyStartIndex, startIndex + propertyStartIndex + size);
        } else {
            console.log(`found nothing for index ${i}`)
        }
    }
    return data;
}

export function loadShard(shard: Interfaces.SBObjectHandle) {
    return new Promise<ArrayBuffer>((resolve, reject) => {
        if (!shard.shardServer) {
            console.error("shardServer not found (2.0 change: required)")
            throw new Error("shardServer not found (2.0 change: required)")
        }
        const codeShardFetch = `${shard.shardServer!}/api/v1/fetchData?id=${shard.id}&type=p&verification_token=${shard.verification}`
        fetch(codeShardFetch)
            .then((res) => res.arrayBuffer())
            .then((payload) => {
                let data = extractPayload(payload)
                if (shard.version == '1') {
                    data.shardKey = base64ToArrayBuffer(shard.key)
                } else if (shard.version == '2') {
                    data.shardKey = base62ToArrayBuffer32(shard.key!)
                } else {
                    reject(`unknown or missing shard version: ${shard}`)
                }
                deCryptShard(data).then((decrypted) => {
                    resolve(decrypted);
                }).catch(() => { reject('unable to decrypt'); })
            })
            .catch((err) => {
                if (`${err}`.match('"ror":"cann"')) reject('shard not found')
                else reject(`failed to fetch or process shard: ${err}`)
            })
    })
}

export function loadLibraryCode(shard: Interfaces.SBObjectHandle) {
    return new Promise<void>((resolve, reject) => {
        loadShard(shard)
            .then((decrypted) => {
                let jslibText = new TextDecoder("utf-8").decode(decrypted);
                const script = document.createElement('script');
                script.textContent = jslibText;
                document.head.append(script);
                console.log("'window.SB' object (library loaded) should be available in the console.")
                resolve()
            })
            .catch(() => { reject('unable to fetch shard'); })
    })
}

